import React from 'react'
import CodeDiv from '../codeDiv/CodeDiv'
import '../Blog.css'
export default function CheetSheetTemplate(props) {
    return (
        <>

            <div className={`flex ${props.divStyle}`}>
                <h4 className={props.headStyle}>{props.heading} </h4>
                <p className={`blogContent ${props.class}`}>{props.des}</p>
                <div className={`imgDiv ${props.imgClass}`}>
                    <img src={props.img} alt={props.img} className={`flex center justifyCenter imgWidth imgStyle ${props.imgCss}`} />
                </div>
            </div>
            <CodeDiv code={props.code} codeLanguage={props.codeLanguage} url={props.url} headerStyle={props.headerStyle} />

        </>
    )
}
