import React from 'react'
import ProjectTemplate from '../../Projects/ProjectTemplate/ProjectTemplate'
import CheetSheetTemplate from '../CheetSheet/CheetSheetTemplate'
import { java } from '../../../utils/constants';
export default function Java(props) {
    return (
        <>
            {props.setProgress(10)}
            <ProjectTemplate name='Java ' mode={props.mode} isValue={props.isValue} >
                <div className="aboutMe blogDiv">
                    {
                        java.map((category) => (
                            <CheetSheetTemplate
                                imgClass={category.imgClass}
                                heading={category.heading}
                                headStyle={category.headStyle}
                                des={category.des}
                                url={category.url}
                                divStyle={category.divStyle}
                                img={category.img}
                                headerStyle={category.headerStyle}
                                class={category.class}
                            />
                        ))
                    }
                </div>
            </ProjectTemplate>
            {props.setProgress(100)}
        </>
    )
}